*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
   background-color: aliceblue;
}

.Flag{
    width: 100%;
    height: 70px;
    background-image: url(/images/Flag_of_Ukraine.svg.png);
    background-size: 100% 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    color: rgb(0, 0, 0);
}

.Navigation_bar{
    width: 100%;
    height: 40px;
    background: linear-gradient(black 30%, orange 70%);
    font-size: 25px;
    display: flex;
    justify-content: center;
    font-family:'Courier New', Courier, monospace;
}

.link_to_home{
    margin-left: 15px;
}

.nav{
    text-decoration: none;
    margin-top: 5px;
    margin-right: 45px;
    color: rgb(255, 255, 255);
}

.home_page{
   display: flex;
   justify-content: center;
   margin-top: 100px;
   align-items: center;
   flex-direction: column;
   
}

.my_foto{
    height: 300px;
    width: 300px;
    background: no-repeat center/100% url(../images/Cat.jpg);
    border-radius: 100%;
}

.introduce{
    height: 300px;
    width: 60%;
    color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.greeting{
    margin-top: 30px;
    font-size: 40px;
}

.profession{
    margin: 20px;
    font-size: 50px;
}

.introduce_myself{
    font-size: 25px;
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.from{
    margin-top: 20px;
}